.header {
	position: absolute;
	text-align: center;
	/* background: linear-gradient(60deg, rgb(17, 19, 32) 0%, rgb(35, 54, 58) 100%); */
	/* background-color: rgb(39, 38, 44); */
	color: white;
	height: 30vh;
	width: 100%;
	z-index: -999;
	top: 80vh;
}

.flex {
	/*Flexbox for containers*/
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.wavesImg {
	position: relative;
	width: 100%;
	height: 40px;
	margin-bottom: -7px;
	/*Fix for safari gap*/
	/* margin-top: 75vh; */
	/* z-index: -100; */
}

/* Animation */

.parallax > use {
	animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.parallax > use:nth-child(1) {
	animation-delay: -1s;
	animation-duration: 7s;
}
.parallax > use:nth-child(2) {
	animation-delay: -2s;
	animation-duration: 10s;
}
.parallax > use:nth-child(3) {
	animation-delay: -3s;
	animation-duration: 13s;
}
.parallax > use:nth-child(4) {
	animation-delay: -4s;
	animation-duration: 20s;
}
@keyframes move-forever {
	0% {
		transform: translate3d(-90px, 0, 0);
	}
	100% {
		transform: translate3d(85px, 0, 0);
	}
}
/*Shrinking for mobile*/
@media (min-width: 1100px) {
	.wavesImg {
		height: 60px;
		min-height: 40px;
	}
	.content {
		height: 20vh;
	}
	h1 {
		font-size: 24px;
	}
	.header {
		top: 85vh;
	}
}

.spread {
	height: 80vh;
	background-color: white;
	z-index: -100;
	margin-bottom: -200px;
	mask-image: linear-gradient(
		to bottom,
		rgba(0, 0, 0, 1),
		rgba(0, 0, 0, 0)
	);
}
