.navbar {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	height: auto;
	background-color: var(--primary-color);
	z-index: 1000;
}

.navbar__container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	top: 0;
	height: 100%;
	background-color: var(--primary-color);
	box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.2);
	border-top-left-radius: 40px;
	border-top-right-radius: 40px;
}

.navbar__menu {
	width: 100%;
	background-color: var(--primary-color);
}

.navbar__name {
	position: fixed;
	top: 0;
	font-size: 20px;
	color: white;
	padding: 10px 20px;
	z-index: 1;
	width: 100%;
	box-shadow: 0 -5px 3px rgba(0, 0, 0, 0.2);
	background-color: var(--primary-color);
	display: block;
	font-weight: bold;
}

.navbar__options__icon {
	position: fixed;
	top: 11px;
	right: 20px;
	/* z-index: 999; */
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
}

svg:not(:root) {
    overflow: block !important;
}

.navbar__options {
	display: grid;
	grid-template-columns: auto auto auto;
	width: 100%;
	
}

.navbar__option {
	color: white;
	margin: 0 10px;
	padding: 10px 10px 10px 10px;
	font-size: 14px;
	display: flex;
	flex-direction: column;
	align-items: center;
	/* font-weight: bold; */
}

.navbar__option:hover {
	cursor: pointer;
	background-color: rgba(0, 0, 0, 0.1);
	border-radius: 5px;
	transition: 0.5s;
}

.navbar__option__icon {
	padding-bottom: 3px;
	padding-top: 20px;
	/* transform: scale(0.9); */
}

.navbar__close__icon {
	padding-left: 93%;
	padding-bottom: 10px;
	color: white;
	bottom: 20px;
	right: 20px;
	padding-right: 20px;
}

.navbar__hide {
	height: 0;
	display: none;
	transition: height 5s 5s;
}

.navbar__menu {
	display: flex;
	flex-direction: column;
}

.navbar__close {
	padding-right: 10px;
}

@media only screen and (min-width: 1100px) {
	.navbar__close__icon,
	.navbar__option__icon,
	.navbar__options__icon,
	.navbar__close {
		display: none !important;
	}
	.navbar {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		height: 80px;
		padding-top: 15px;
		background-color: var(--primary-color);
	}

	.navbar__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		top: 0;
		height: 100%;
		background-color: var(--primary-color);
		/* z-index: 1000; */
		box-shadow: none;
		border-radius: none;
	}

	.navbar__options {
		margin: 0 50px 0 0;
		display: flex;
		position: relative;
		right: 0;
	}

	.navbar__name {
		position: static;
